<script>
import { mapActions } from "vuex";
//import VueRouter from 'vue-router';
export default {
  data() {
    return {
      title: "",
      content: "",
      todos: [],
    };
  },
  methods: {
    ...mapActions(["addNote"]),
    saveData() {
      this.$router.push("/");
    },
    createNote() {
      const newNote = {
        title: this.title,
        content: this.content,
      };
      this.addNote(newNote);
      this.saveData();
    },
  },
};
</script>

<template>
  <div class="row">
    <ul class="notes form">
      <li>
        <div class="lazur-bg">
          <form @submit.prevent="createNote">
            <input
              v-model="title"
              class="title"
              placeholder="Add title"
              required
            />
            <textarea
              v-model="content"
              class="title"
              placeholder="Add content"
              required
            >
            </textarea>
            <div style="text-align: center">
              <button id="task">Add task</button>
            </div>
          </form>
        </div>
      </li>
    </ul>
  </div>
</template>
